import { Box } from "@mui/material";
import React, { PureComponent, useEffect, useState } from "react";
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from "recharts";

export default function TwoChart() {
  const [data, setData] = useState<Array<Record<string, number | string>>>([]);
  useEffect(() => {
    const dataG: Array<Record<string, number | string>> = [];
    for (var i = 0; i < 1000; i++) {
      dataG.push({
        name: new Date(i * 1000).toLocaleTimeString(),
        Temp: (Math.random() * 10).toFixed(2),
        GL: (Math.random() * 5).toFixed(2),
        amt: (Math.random() * 1000).toFixed(2),
      });
    }
    setData(dataG);
  }, []);

  return (
    <Box
      style={{
        display: "flex",
        flexDirection: "column",
        flex: 1,
        minWidth: 4 * 1000,
        width: 4 * 100,
      }}
    >
      <Box
        style={{
          display: "flex",
          padding: "10px",
        }}
      >
        <h4>Temperatura</h4>
      </Box>
      <ResponsiveContainer width="100%" height={200}>
        <AreaChart
          width={500}
          height={200}
          data={data}
          syncId="anyId"
          margin={{
            top: 10,
            right: 30,
            left: 0,
            bottom: 0,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" />
          <YAxis />
          <Tooltip
            content={(props) => {
              return (
                <div
                  style={{
                    backgroundColor: "white",
                  }}
                >
                  <h4>
                    Horário: {props.payload && props?.payload[0]?.payload?.name}
                  </h4>
                  <h4
                    style={{
                      color: "#8884d8",
                    }}
                  >
                    Temp: {props.payload && props?.payload[0]?.payload?.Temp} C
                  </h4>
                  <h4
                    style={{
                      color: "blue",
                    }}
                  >
                    Litros: {props.payload && props?.payload[0]?.payload?.amt} L
                  </h4>
                </div>
              );
            }}
            payload={[
              {
                name: "Temp",
                value: "Temp",
              },
            ]}
          />
          <Area
            type="monotone"
            dataKey="Temp"
            stroke="#8884d8"
            fill="#8884d8"
          />
        </AreaChart>
      </ResponsiveContainer>
      <Box sx={{ display: "flex", padding: "10px" }}>
        <h4>GL</h4>
      </Box>

      <ResponsiveContainer width="100%" height={200}>
        <AreaChart
          width={500}
          height={200}
          data={data}
          syncId="anyId"
          margin={{
            top: 10,
            right: 30,
            left: 0,
            bottom: 0,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" />
          <YAxis />
          <Tooltip
            content={(props) => {
              return (
                <div
                  style={{
                    backgroundColor: "white",
                  }}
                >
                  <h4>
                    Horário: {props.payload && props?.payload[0]?.payload?.name}
                  </h4>
                  <h4
                    style={{
                      color: "#82ca9d",
                    }}
                  >
                    GL: {props.payload && props?.payload[0]?.payload?.GL} C
                  </h4>
                  <h4
                    style={{
                      color: "blue",
                    }}
                  >
                    Litros: {props.payload && props?.payload[0]?.payload?.amt} L
                  </h4>
                </div>
              );
            }}
            payload={[
              {
                name: "GL",
                value: "GL",
              },
            ]}
          />
          <Area type="monotone" dataKey="GL" stroke="#82ca9d" fill="#82ca9d" />
        </AreaChart>
      </ResponsiveContainer>
    </Box>
  );
}
