import * as React from "react";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";

export default function RadioButton(props: any) {
  return (
    <FormControl {...props}>
      <FormLabel id="demo-row-radio-buttons-group-label">Parte</FormLabel>
      <RadioGroup
        defaultValue="cabeça"
        row
        aria-labelledby="demo-row-radio-buttons-group-label"
        name="row-radio-buttons-group"
      >
        <FormControlLabel value="cabeça" control={<Radio />} label="Cabeça" />
        <FormControlLabel value="coração" control={<Radio />} label="Coração" />
        <FormControlLabel value="cauda" control={<Radio />} label="Cauda" />
        <FormControlLabel value="todos" control={<Radio />} label="Todos" />
      </RadioGroup>
    </FormControl>
  );
}
